import React, { useCallback } from "react";
import { withRouter } from "react-router";
import logo from './money.svg';
import vk from './vk.png';
import inst from './inst.png';
import git from './git.png';
import app from "./base";
import "./App.css";
import {
  Link
} from "react-router-dom";

const SignUp = ({ history }) => {
  const handleSignUp = useCallback(async event => {
    event.preventDefault();
    const { email, password } = event.target.elements;
    try {
      await app
        .auth()
        .createUserWithEmailAndPassword(email.value, password.value);
      history.push("/home");
    } catch (error) {
      alert(error);
    }
  }, [history]);

  return (
    
    <div> 
            <div className="header">
                    <div className="wrapper">
                        <div className="logo">
                            <div className="logo-pic">
                                <img src={logo} className="logo-pic__img" alt="logo" />
                            </div>
                            <div className="logo-word">
                                iMoney&nbsp;
                            </div>
                            {/* <div className="test">
                                &nbsp;.com&nbsp;
                            </div> */}
                        </div>
                        <div>
                        <Link to="/" className="btn btn-outline-light" role="button" aria-pressed="true">Back</Link> 
                        
                    </div>
                </div>
      </div>
      
    <div className="card card-body col-3 mx-auto marginTop bot">
      <h2 className="pl-2 lin">Sign up</h2>
      <form onSubmit={handleSignUp}>
      <label className="pr-4">
          <input className="form-control" name="Name" type="text" placeholder="Name" />
        </label>
        <label className="pr-4">
          <input className="form-control" name="email" type="email" placeholder="Email" />
        </label>
        <label className="pr-4">
          <input className="form-control" name="password" type="password" placeholder="Password" />
        </label>
        <div class="buttons">
        <button type="submit" className="btn b2 btn-outline-secondary">Sign Up</button>
        <Link to="/login" className="btn b2 btn-outline-secondary" role="button" aria-pressed="true">Go to LogIn</Link>
      </div>
      </form>
    </div>
    <div className="iiimmgg">
                {/* <img src={logoo} className="iimmgg" alt="logoo" />
                 */}</div>  
                <div className="footer">
                <div className="wrap">
                    
                    <div className="fot">
                            <div className="fot-pic">
                                <img src={logo} className="v" alt="logo" />
                            </div>
                            <div className="logo-word">
                                iMoney&nbsp;
                            </div>
                            
                    </div>
                    <div className="social">
                    <a href="https://vk.com/andgen"  class="social-item">
                            <img src={vk} alt="" class="social-pic" />
                        </a>
                        <a href="#!" class="social-item">
                            <img src={inst} alt="" class="social-pic" />
                        </a>
                        <a href="https://github.com/Andrey-vorobey123" class="social-item">
                            <img src={git} alt="" class="social-pic" />
                        </a>
                    </div>
                    </div>
                </div>
    </div>
  );
};

export default withRouter(SignUp);
