import React, { Component } from "react";
import app from "./base";
import logo from './money.svg';
import vk from './vk.png';
import inst from './inst.png';
import git from './git.png';
import "./App.css"

class Converter extends Component {
  state = {
    currencies: ["USD", "AUD", "SGD", "PHP", "EUR", "RUB"],
    base: "USD",
    amount: "",
    convertTo: "RUB",
    result: "",
    date: ""
  };

  handleSelect = e => {
    this.setState(
      {
        [e.target.name]: e.target.value,
        result: null
      },
      this.calculate
    );
  };

  handleInput = e => {
    this.setState(
      {
        amount: e.target.value,
        result: null,
        date: null
      },
      this.calculate
    );
  };

  calculate = () => {
    const amount = this.state.amount;
    if (amount === isNaN) {
      return;
    } else {
      fetch(`https://api.exchangeratesapi.io/latest?base=${this.state.base}`)
        .then(res => res.json())
        .then(data => {
          const date = data.date;
          const result = (data.rates[this.state.convertTo] * amount).toFixed(2);
          this.setState({
            result,
            date
          });
        });
    }
  };

  handleSwap = e => {
    const base = this.state.base;
    const convertTo = this.state.convertTo;
    e.preventDefault();
    this.setState(
      {
        convertTo: base,
        base: convertTo,
        result: null
      },
      this.calculate
    );
  };
  render() {
    const { currencies, base, amount, convertTo, result, date } = this.state;
    return (
      <div>
        <div className="header">
                    <div className="wrapper">
                        <div className="logo">
                            <div className="logo-pic">
                                <img src={logo} className="logo-pic__img" alt="logo" />
                            </div>
                            <div className="logo-word">
                                iMoney&nbsp;
                            </div>
                            {/* <div className="test">
                                &nbsp;.com&nbsp;
                            </div> */}
                        </div>
                        <div>
                        <button className="btn btn-outline-light" onClick={() => app.auth().signOut()}>Sign out</button>
                        
                    </div>
                </div>
      </div>
    
      <div className="container my-5 bot"> 
        <div className="row">
          <div className="col-lg-5 mx-auto">
            <div className="card card-body ">
              <h4 className="pl-3 lin r mb">
                {amount} {base} &nbsp;=&nbsp;
              
                {amount === " "
                  ? "0"
                  : result === null
                  ? "Calculating..."
                  : result}{" "}
                {convertTo}
              </h4>
              {/* <p className="pl-3">{amount === "" ? "" : date === null ? "" : "now: "+date}</p>
               */}<div className="row">
                <div className="col-lg-12">
                  <form className="form-inline mb-4">
                    <input
                      type="number"
                      value={amount}
                      onChange={this.handleInput}
                      className="form-control form-control-lg mx-3"
                    />
                    <select
                      name="base"
                      value={base}
                      onChange={this.handleSelect}
                      className="form-control form-control-lg"
                    >
                      {currencies.map(currency => (
                        <option key={currency} value={currency}>
                          {currency}
                        </option>
                      ))}
                    </select>
                  </form>

                  <form className="form-inline mb-4">
                    <input
                      disabled={true}
                      value={
                        amount === ""
                          ? "0"
                          : result === null
                          ? "Calculating..."
                          : result
                      }
                      className="form-control form-control-lg mx-3"
                    />
                    <select
                      name="convertTo"
                      value={convertTo}
                      onChange={this.handleSelect}
                      className="form-control form-control-lg"
                    >
                      {currencies.map(currency => (
                        <option key={currency} value={currency}>
                          {currency}
                        </option>
                      ))}
                    </select>
                  </form>
                </div>

                
              </div>
              <p className="pl-3">{amount === "" ? "" : date === null ? "" : "now: "+date}</p>
            </div>
          </div>
        </div>
      </div>
      <div className="iiimmgg">
                {/* <img src={logoo} className="iimmgg" alt="logoo" />
                 */}</div>  
                <div className="footer">
                <div className="wrap">
                    
                    <div className="fot">
                            <div className="fot-pic">
                                <img src={logo} className="v" alt="logo" />
                            </div>
                            <div className="logo-word">
                                iMoney&nbsp;
                            </div>
                            
                    </div>
                    <div className="social">
                    <a href="https://vk.com/andgen"  class="social-item">
                            <img src={vk} alt="" class="social-pic" />
                        </a>
                        <a href="#!" class="social-item">
                            <img src={inst} alt="" class="social-pic" />
                        </a>
                        <a href="https://github.com/Andrey-vorobey123" class="social-item">
                            <img src={git} alt="" class="social-pic" />
                        </a>
                    </div>
                    </div>
                </div>
      </div>
    );
  }
}

export default Converter;