import React, {Component} from "react";
import "./App.css";
import logo from './money.svg';
import vk from './vk.png';
import inst from './inst.png';
import git from './git.png';
import { withRouter } from "react-router";
import {
    Link
  } from "react-router-dom";


class Welcome extends Component
{
    componentDidMount(){
        fetch('https://api.exchangeratesapi.io/latest?base=RUB')
        .then(function (resp1) { return resp1.json() })
        .then( (blank)=> {
            console.log(blank.rates);
            this.setState({curses:blank.rates})
        })
    }
    
    render(){
        const curses = this.state.curses;
        return(     
            <div> 
                <div className="header">
                    <div className="wrapper">
                        <div className="logo">
                            <div className="logo-pic">
                                <img src={logo} className="logo-pic__img" alt="logo" />
                            </div>
                            <div className="logo-word">
                                iMoney&nbsp;
                            </div>
                            {/* <div className="test">
                                &nbsp;.com&nbsp;
                            </div> */}
                        </div>
                        <div>
                        <Link to="/login" className="btn pr btn-outline-light" role="button" aria-pressed="true">Log In</Link> 
                        <Link to="/signup" className="btn btn-outline-light" role="button" aria-pressed="true">Sign Up</Link></div>
                    </div>
                </div>
            
                
                  <div className="cursesBlock container me-5 mbb">
                    <div className="col-lg-4 mx-auto card card-body marginTop">
                    <h2 className="pl-2 test">Currency rate</h2>
                      <div className="pl-2 textRub">
                        1 EUR = {(1 / curses.EUR).toFixed(4)} RUB
                       </div> 
                       <div className="pl-2 textRub">
                       1 USD = {(1 / curses.USD).toFixed(4)} RUB
                       </div> 
                       <div className="pl-2 textRub">
                       1 GBP = {(1 / curses.GBP).toFixed(4)} RUB
                       </div> 
                       <div className="pl-2 textRub">
                       1 CNY = {(1 / curses.CNY).toFixed(4)} RUB
                       </div> 
                       <div className="pl-2 textRub">
                       1 JPY = {(1 / curses.JPY).toFixed(6)} RUB
                       </div>
                   </div> 
                </div>
                <div className="iiimmgg">
                {/* <img src={logoo} className="iimmgg" alt="logoo" />
                 */}</div>  
                <div className="footer">
                <div className="wrap">
                    
                    <div className="fot">
                            <div className="fot-pic">
                                <img src={logo} className="v" alt="logo" />
                            </div>
                            <div className="logo-word">
                                iMoney&nbsp;
                            </div>
                            
                    </div>
                    <div className="social">
                    <a href="https://vk.com/andgen"  class="social-item">
                            <img src={vk} alt="" class="social-pic" />
                        </a>
                        <a href="#!" class="social-item">
                            <img src={inst} alt="" class="social-pic" />
                        </a>
                        <a href="https://github.com/Andrey-vorobey123" class="social-item">
                            <img src={git} alt="" class="social-pic" />
                        </a>
                    </div>
                    </div>
                </div>
                     
            </div>
        );
    }
    state = {
        curses: 0
    }
}
export default withRouter(Welcome);